import IonIcon from "@reacticons/ionicons";
import { useAppSelector } from "../../../../hooks/useRedux";
import { useEffect, useState } from "react";
import { collection, doc, getDoc, getDocs, query, where } from "firebase/firestore";
import { db } from "../../../../configs/firebase.config";

export default function Surplus() {

    const uid = useAppSelector(state => state.auth.uid)
    const [balance, setBalance] = useState<any>(null)



    const getBalance = async () => {
        const balanceRef = doc(db, 'users', uid)
        const balanceSnap = await getDoc(balanceRef)
        setBalance(balanceSnap.data())
    }

    useEffect(() => {
        getBalance()
    }, [])

    console.log(balance);


    const formatMoney = (number: number) => {
        number = Math.round(number * 1000) / 1000;

        return number
            .toFixed(0)
            .replace(/\D/g, "")
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }


    return (
        <div className="bg-[#EEEEEE] p-2 rounded-lg">
            <div className="flex justify-between items-start">
                <p className="font-semibold text-sm text-[#383838]">Số dư ví</p>
                <div className="flex flex-col items-end gap-2">
                    <IonIcon name="eye" className="text-3xl" />
                    <span className="text-[#ce4f53] font-bold text-lg">{formatMoney(balance?.balance)} VND</span>
                </div>
            </div>
        </div>
    )
}
