import IonIcon from "@reacticons/ionicons";
import IMAGES from "../../../../assets";
import { doc, getDoc } from "firebase/firestore";
import { db } from "../../../../configs/firebase.config";
import { useAppSelector } from "../../../../hooks/useRedux";
import { useEffect, useState } from "react";
import Loading from "../../../../components/shared/Loading";

export default function Card() {

    const uid = useAppSelector(state => state.auth.uid)
    const [account, setAccount] = useState<any>(null)

    const getAccount = async () => {
        const docRef = doc(db, "users", uid);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
            setAccount(docSnap.data());
        } else {
            console.log("No such document!");
        }
    }

    useEffect(() => {
        getAccount()
    }, [])

    if (!account) <Loading />

    return (
        <div className="relative">
            <img src={IMAGES.Card} alt="" />
            <div className="absolute top-4 left-4 text-white font-medium text-xl">{account?.nameBank ? account?.nameBank : 'Chưa cập nhật'}</div>
            <div className="absolute bottom-4 left-4 text-white flex flex-col">
                <span>{account?.numberBank ? account?.numberBank : '*******************'}</span>
                <span className="uppercase">{account?.nameAccount ? account?.nameAccount : '*******************'}</span>
            </div>
        </div>
    )
}
